<template>
  <div class="container mt-4">
    <h1>Catalogues</h1>
    <catalog-filter-bar
      :campaigns="campaigns"
      v-model:selectedCampaign="selectedCampaign"
      v-model:selectedStatus="selectedStatus"
      v-model:sortBy="sortBy"
    />
    <div v-if="loading" class="spinner-border" role="status">
      <span class="visually-hidden">Loading...</span>
    </div>
    <div v-if="error" class="alert alert-danger">{{ error }}</div>
    <div v-if="catalogs.length" class="catalog-grid">
      <router-link
        v-for="catalog in catalogs"
        :key="catalog.documentId"
        :to="{ name: 'CatalogDetail', params: { id: catalog.documentId } }"
        class="catalog-card"
      >
        <h5 class="catalog-card-title">{{ catalog.title }}</h5>
        <div class="catalog-card-image" :style="{ backgroundImage: 'url(' + getCoverImageUrl(catalog) + ')' }"></div>
        <div class="catalog-card-footer">
          <span>{{ catalog.number_of_pages }} pages</span>
          <span><i class="bi bi-calendar me-1"></i>{{ formatDate(catalog.publication_date) }}</span>
        </div>
      </router-link>
    </div>
    <div v-else-if="!loading">
      <p>No catalogs found.</p>
    </div>
  </div>
</template>

<script>
import strapiApi from '@/services/strapiApi';
import CatalogFilterBar from '@/components/CatalogFilterBar.vue';

export default {
  name: 'CatalogList',
  components: {
    CatalogFilterBar,
  },
  data() {
    return {
      catalogs: [],
      campaigns: [],
      selectedCampaign: '',
      selectedStatus: '',
      sortBy: 'publication-date-desc',
      loading: true,
      error: null,
      strapiBaseUrl: process.env.VUE_APP_STRAPI_URL,
    };
  },
  watch: {
    selectedCampaign(newValue) {
      localStorage.setItem('catalogSelectedCampaign', newValue);
      this.fetchCatalogs();
    },
    selectedStatus(newValue) {
      localStorage.setItem('catalogSelectedStatus', newValue);
      this.fetchCatalogs();
    },
    sortBy(newValue) {
      localStorage.setItem('catalogSortBy', newValue);
      this.fetchCatalogs();
    },
  },
  async created() {
    const savedCampaign = localStorage.getItem('catalogSelectedCampaign');
    if (savedCampaign) {
      this.selectedCampaign = savedCampaign;
    }
    const savedStatus = localStorage.getItem('catalogSelectedStatus');
    if (savedStatus) {
      this.selectedStatus = savedStatus;
    }
    const savedSortBy = localStorage.getItem('catalogSortBy');
    if (savedSortBy) {
      this.sortBy = savedSortBy;
    }
    
    await this.fetchCatalogs();

    try {
      const campaignsResponse = await strapiApi.getCampaigns();
      this.campaigns = campaignsResponse.data.data;
    } catch (err) {
      this.error = 'Failed to fetch campaigns.';
      console.error(err);
    } finally {
      this.loading = false;
    }
  },
  methods: {
    async fetchCatalogs() {
      this.loading = true;
      this.error = null;
      try {
        const response = await strapiApi.getCatalogs(this.selectedCampaign, this.sortBy, this.selectedStatus);
        this.catalogs = response.data.data;
      } catch (err) {
        this.error = 'Failed to fetch catalogs.';
        console.error(err);
      } finally {
        this.loading = false;
      }
    },
    formatDate(dateString) {
      if (!dateString) return '';
      const options = { year: 'numeric', month: 'long', day: 'numeric' };
      return new Date(dateString).toLocaleDateString('fr-FR', options);
    },
    getStrapiMediaUrl(path) {
      if (!path) return '';
      return `${this.strapiBaseUrl}${path}`;
    },
    getCoverImageUrl(catalog) {
      if (!catalog.pages || !catalog.pages.length) return '';

      let firstPage = catalog.pages.find(p => p.page_number === 1);
      if (!firstPage) {
        firstPage = catalog.pages.reduce((minPage, p) => p.page_number < minPage.page_number ? p : minPage, catalog.pages[0]);
      }

      if (firstPage && firstPage.active_image_version && firstPage.active_image_version.fichier_jpeg) {
        return this.getStrapiMediaUrl(firstPage.active_image_version.fichier_jpeg.url);
      }

      return '';
    },
  },
};
</script>

